.container {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(112.1deg, rgb(32, 38, 57) 11.4%, rgb(63, 76, 119) 70.2%);
}

.calculator {
  padding: 20px;
  border-radius: 15px; /* Removed duplicate border-radius */
  background-color: white; /* Fixed syntax error (removed background color) */
}

form input {
  border: none;
  outline: 0;
  width: 60px;
  height: 60px;
  font-size: 16px;
  margin: 2px;
  border-radius: 10px;
  color: black;
  font-weight: bolder;
  cursor: pointer;
}

form input[type="button"]:hover {
  background-color: orange;
}

.display {
  display: flex;
  justify-content: flex-end;
  margin: 5px 0 15px 0;
}

.display input {
  text-align: right;
  flex: 1;
  font-size: 40px;
  padding: 5px 10px;
  color: white;
  background-color: rgb(253, 170, 97);
}

.equal {
  width: 123px;
}

